	module.exports = {"data":{"markdownRemark":{"html":"<p>夢終劇.NETですが、バグだらけの状態でリリースしてしまったことをお詫び申し上げます。必ずアップデートをリリースいたしますので、それまでは同梱の弾幕風版をお楽しみいただけると幸いです。</p>\n<p>ついては1日でも早くアップデートを提供できるよう、<em>急遽プログラマを1名募集いたします</em>。前回募集で1名も集まらなかったため、再募集いたします。</p>\n<ul>\n<li>募集締切：<del>2012/2/13 (月)</del>(〆切りました。今回は数名の方のご応募がありました。誠にありがとうございます)</li>\n<li>募集人員：1名</li>\n<li>担当箇所：雑魚実装・ステージ配置(C#。希望次第では別の箇所も追加でご担当戴けます)</li>\n<li>想定工期：4か月(現状把握・学習に0.5か月、実装に3.5か月。プログラムの熟練度にもよりますが、実際にはもう少し短期で仕上がると思います)</li>\n<li>報酬：10万円 + 夢終劇DXプレス版1～10枚(最大10枚までのご希望枚数) + プロジェクト一式(二次利用は若干制限事項があります。詳細はお問い合わせください)</li>\n<li>\n<p>その他：スペック確認のため、募集〆切前に若干質問をすることがありますが、ご回答にご協力お願いします。</p>\n<h2>必須スペック</h2>\n</li>\n</ul>\n<ul>\n<li><a href=\"/!/thC/nph-thC3.0TrGetNightlyBuild.cgi\">夢終劇.NET体験版</a>が正常に起動・動作可能な環境を持っていること。</li>\n<li>プログラム基礎(変数関数、クラス、条件分岐くらいまで)は最低限理解していること。</li>\n<li>C++、Java(J#含む)、C#、PHP(4以降)のいずれか1つ、またはそれ以上でちょっとは組める程度のプログラミングスキル。(いずれかが理解できるならC#も理解しやすいかと)</li>\n<li>\n<p>明瞭な意思主張、報告連絡相談ができること。</p>\n<h2>優遇スペック</h2>\n</li>\n</ul>\n<ul>\n<li>下記に関しては必須ではありませんが、応募が多数となった場合、下記スペックをお持ちの方は優遇します。</li>\n<li>C#およびXNAに精通している。</li>\n<li>SubversionおよびTracの扱いを心得ている。</li>\n<li>でかいゲームの中身を知りたい、そうでなくても、もっとプログラミングの勉強をしたい。</li>\n<li>東京圏まで3時間以内の日本国内在住。</li>\n<li>\n<p>STG、または東方が大好き！</p>\n<h2>応募方法</h2>\n</li>\n</ul>\n<ul>\n<li><a href=\"https://www1n.sppd.ne.jp/danmaq.com/e-danmaq/index.cgi?type=shopinfo\">e-danmaq</a>のお問い合わせ先E-Mailへ、下記情報を本文に入れて送信してください。</li>\n<li>件名：先頭に&#x3C;夢終劇>を入れてください。</li>\n<li>名前、ハンドル名、Webページ(あれば)、上記スペックをどれだけ満たしているか。</li>\n<li>その他スキル自慢、実績自慢など(捏造誇張はNGですが、俺これだけやったんだぜ、と自己主張できる方は凄い優遇します)\nどうか、ご参加のほどよろしくお願いします。</li>\n</ul>","frontmatter":{"cover":null,"date":"2012-01-30T11:42:17+09:00","strDate":"2012/1/30","title":"[再募集]夢終劇(.NET版)のプログラマ募集します"}}},"pathContext":{"path":"/ja/blog/2012/01/30-再募集夢終劇-net版のプログラマ募集します/","langKey":"ja"}}

